import { Navbar, Container, Nav } from 'react-bootstrap';

function NavHeader() {
  return (
    <>
      <Navbar bg="dark" variant="dark">
        <Container>
          <Navbar.Brand href="/">Genki Dama</Navbar.Brand>
          <Nav className="me-auto">
            <Nav.Link href="/repositories">Repositories</Nav.Link>
            <Nav.Link href="/projects">Projects</Nav.Link>
            <Nav.Link href="/explore">Explore</Nav.Link>
            <Nav.Link href="/pricing">Plans</Nav.Link>
          </Nav>
          <Navbar.Brand href="/sign-in">Sign-In</Navbar.Brand>
          <Navbar.Brand href="/sign-up">Sign-Up</Navbar.Brand>
        </Container>
      </Navbar>
    </>
  );
  
}

export default NavHeader;